@import url('https://fonts.googleapis.com/css2?family=Satisfy&display=swap');
@import './resets';
@import './variables';

@mixin tablet {
    @media (min-width: $tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: $desktop) {
        @content;
    }
}

@mixin small-screen {
    @media (max-width: $small-screen) {
        @content;
    }
}

@mixin fadeAnim {
    opacity: 0;
    animation: fadeInAnimation ease 2s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

body {
    background-color: $bg-main;
    color: $font-main;
    font-family: Helvetica, Arial, sans-serif;
}

.content {
    
    .titleDiv {
        padding-top: 30vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        .title {
            font-family: 'Satisfy', cursive;
            margin-bottom: 1rem;
            font-size: 3rem;

            @include fadeAnim();
            animation-delay: 200ms;

            @include small-screen{
                font-size: 2rem;
            }

            @include tablet{
                font-size: 3.5rem;
            }

            @include desktop{
                font-size: 4rem;
            }
        }

        .desc {
            @include fadeAnim();
            animation-delay: 400ms;

            @include tablet{
                font-size: 1.5rem;
            }

            @include desktop{
                font-size: 2rem;
            }

        }

        .skillset {
            @include fadeAnim();
            animation-delay: 600ms;

            margin-top: 1rem;
            .logo{
                margin: 0 5px 0 5px;
                height: 40px;
            }
        }
    }

    .projectsDiv {
        margin-top: 20vh;

        .project {
            margin-top: 50vh;

            .projectTitle {
                margin-bottom: 40vh;
            }
        }
    }
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
        transform: translate(0 , 120px);
    }

    100% {
        opacity: 1;
        transform: translate(0, 0);
    }
}
